<template>
  <div>
    <!-- <h1>{{ msg }}</h1> -->
    <!-- header -->
    <Head/>
    <!-- main -->
    <div>
        <div class="vc-element">
            <!-- <div class="background-overlay">
                // ~是相对于其他路径（文件）的，类似于相对路径
                // ~@/assets/scss/_variables.scss   ==>>表示相对于@（别名，一般是src目录）下的 assets/scss/_variables.scss；
                // 页面引入图片不使用@前不加~也会报错
                <img src="~@/assets/img/bg_1.jpg"/>
            </div> -->
            <div class="demo-image__lazy">
                <el-image v-for="url in urls" :key="url" :src="url" lazy class="img"></el-image>
            </div>
        </div>
    </div>
    <!-- footer -->
  </div>
</template>

<script>
import Head from '@/components/Header'
const bgUrl1 = require('@/assets/img/bg_1.jpg')
export default {
  name: 'HelloWorld',
  components: {
    Head
  },
  data () {
    return {
      msg: 'Welcome To Wu Chgan Personal Homepage',
      urls: [
        bgUrl1
      ]
    }
  },
  methods: {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body.compose-mode .vc-element {
    display: block;
    position: relative;
}
.background-overlay {
    /* position: absolute; */
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    overflow-x: hidden;
}
.img{
    width: 100%;
    height: inherit;
}
</style>



