<template>
    <div>

        <Head/>
        <!-- 背景音乐 -->
        <div>
            <el-row>
                <el-col :span="4">
                    <el-popover placement="top-start" trigger="hover">
                        <div style="text-align: center">
                            <el-progress
                            color="#67C23A"
                            type="circle"
                            :percentage="music.volume"></el-progress><br>
                                <el-button
                                @click="changeVolume(-10)"
                                icon="el-icon-minus"
                                circle></el-button>
                                <el-button
                                @click="changeVolume(10)"
                                icon="el-icon-plus"
                                circle></el-button>
                        </div>
                        <el-button
                            @click="play"
                            id="play"
                            slot="reference"
                            :icon="music.isPlay?'el-icon-refresh':'el-icon-caret-right'"
                            circle></el-button>
                    </el-popover>
                </el-col>
                <el-col :span="14"  style="padding-left: 20px">
                    <el-slider
                        @change="changeTime"
                        :format-tooltip="formatTime"
                        :max="music.maxTime"
                        v-model="music.currentTime"
                        style="width: 100%;"></el-slider>
                </el-col>
                <el-col :span="6" style="padding: 9px 0px 0px 10px;color:#909399;font-size: 13px">
                    {{formatTime(music.currentTime)}}/{{formatTime(music.maxTime)}}
                </el-col>
            </el-row>
            <audio ref="music" loop autoplay>
                <source src="@/assets/audio/dthsz.mp3" type="audio/mpeg">
            </audio>
        </div>

        <div>
            <section id="main-slider" class="no-margin">
                <div class="block">
                    <el-carousel arrow="never" indicator-position="none" :height="imgHeight+'px'">
                        <el-carousel-item v-for="item in urls" :key="item">
                            <el-row>
                                <el-col>
                                    <img :src="item" alt="" class="bannerImg" ref="imgHeight" @load="imgLoad" style="max-height: 900px;">
                                </el-col>
                            </el-row>
                        </el-carousel-item>
                    </el-carousel>
                </div>
            </section>
        </div>

    </div>
</template>

<script>
import Head from '@/components/Header'
const bgUrl2 = require('@/assets/img/bg_2.jpg')
const bgUrl3 = require('@/assets/img/bg_3.jpg')
const bgUrl4 = require('@/assets/img/bg_4.jpg')
export default {
    components: {
        Head
    },
    data () {
    return {
      imgHeight: '150px',
      urls: [
        bgUrl2,
        bgUrl3,
        bgUrl4
      ],
      music: {
        isPlay: false,
        currentTime: 0,
        maxTime: 0,
        volume: 100
      }
    }
  },
  mounted () {
    window.addEventListener('resize', () => { //  resize:当调整浏览器窗口的大小时触发事件
      this.imgHeight = this.$refs.imgHeight[0].height
      this.imgLoad()
    })
    this.$nextTick(() => {
        setInterval(this.listenMusic, 1000)
    })
  },
  methods: {
      imgLoad() {
        this.$nextTick(() => {
            this.imgHeight = this.$refs.imgHeight[0].height;
        })
    },
    listenMusic() {
        if (!this.$refs.music) {
          return
        }
        if (this.$refs.music.readyState) {
          this.music.maxTime = this.$refs.music.duration
        }
        this.music.isPlay = !this.$refs.music.paused
        this.music.currentTime = this.$refs.music.currentTime
      },
      play() {
        if (this.$refs.music.paused) {
          this.$refs.music.play()
        } else {
          this.$refs.music.pause()
        }
        this.music.isPlay = !this.$refs.music.paused
        this.$nextTick(() => {
          document.getElementById('play').blur()
        })
      },
      changeTime(time) {
        this.$refs.music.currentTime = time
      },
      changeVolume(v) {
        this.music.volume += v
        if (this.music.volume > 100) {
          this.music.volume = 100
        }
        if (this.music.volume < 0) {
          this.music.volume = 0
        }
        this.$refs.music.volume = this.music.volume / 100
      },
      formatTime(time) {
        const it = parseInt(time)
        const m = parseInt(it / 60)
        const s = parseInt(it % 60)
        return (m < 10 ? '0' : '') + parseInt(it / 60) + ':' + (s < 10 ? '0' : '') + parseInt(it % 60)
      }
  }
}
</script>

<style scoped>
.no-margin {
    margin: 0;
    padding: 0;
}
.bannerImg {
    width: 100%;
    height: inherit;
    /* min-height: 360px;
    min-width: 1400px; */
  }
  .el-slider__runway {
    display: none;
  }
</style>



